/*** Overrides for react-virtualized styles ***/
.ReactVirtualized__Table__Grid {
  border-bottom: 1px solid #24494E;
  border-right: 1px solid #24494E;
  background: #34495E;
}
/** Always show scrollbar. */
.ReactVirtualized__Table__Grid::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 8px;
}
.ReactVirtualized__Table__Grid::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}
.ReactVirtualized__Table__headerColumn {
  align-items: center;
  border-right: 1px solid #24494E;
  display: flex;
  flex: 1;
  height: 100%;
  min-width: 0;
  justify-content: center;
  padding-right: 10px;
}
.ReactVirtualized__Table__headerColumn:last-child {
  border-right: none;
}
.ReactVirtualized__Table__headerRow {
  background: #24394E;
  border: 1px solid #24494E;
  padding-right: 0 !important;
  text-transform: none;
  color: #dddd49;
}
.ReactVirtualized__Table__row {
  color: #fff;
  border-bottom: 1px solid #24494E;
  border-left: 1px solid #24494E;
}
.ReactVirtualized__Table__rowColumn {
  align-items: center;
  border-right: 1px solid #24494E;
  display: flex;
  flex: 1;
  height: 100%;
  min-width: 0;
  justify-content: flex-end;
  padding-right: 10px;
}
.ReactVirtualized__Table__rowColumn:last-child {
  border-right: none;
}
.oddRow {
  background: #44596E;
}
.selectedRow {
  background: #74899E;
  border-bottom: 0px;
  border-top: 0px;
}
.text {
  justify-content: flex-start;
}
